import ContainerComponent from "./ContainerComponent";
export default class VideoComponent extends ContainerComponent {
    $mount() {
        super.$mount();
        const video = this.targetElement;
        video.src = this.getAttribute("src");
        video.loop = true;
        this.video = video;
    }
    $slideStart() {
        this.video.play();
    }
    generateTag() {
        return document.createElement("video");
    }
}
VideoComponent.attributes = {
    inBuild: {
        converter: "Number",
        default: 0
    },
    src: {
        converter: "String",
        default: 0
    },
    outBuild: {
        converter: "Number",
        default: Number.MAX_SAFE_INTEGER
    },
    defaultContainer: {
        converter: "String",
        default: "div#paragraph-root"
    }
};



